<script>
	import * as echarts from 'echarts'
	export default {
		data() {
			return {
				// 状态栏高度
				statusBarHeight: 0,
				// 导航栏高度
				navBarHeight: 82 + 11,
				percent: 71.094,
				option: {
					xAxis: {
						data: ['4-20', '4-19', '4-18', '4-17', '4-16']
					},
					yAxis: {
						position: 'right'
					},
					series: [{
						type: 'candlestick',
						data: [
							[20, 34, 10, 38],
							[40, 35, 30, 50],
							[31, 38, 33, 44],
							[38, 15, 5, 42],
							[31, 38, 33, 44]
						]
					}]
				}
			}
		},
		created() {
			//获取手机状态栏高度
			this.statusBarHeight = uni.getSystemInfoSync()['statusBarHeight'];
		},
		methods: {
			async init() {
				// chart 图表实例不能存在data里
				const chart = await this.$refs.chart.init(echarts);
				chart.setOption(this.option)
			},
			changeNavShow() {
				console.log(1)
			}
		}
	}
